 .inputfile {
	position: relative;
	display: inline-block;
    left:-119px;
}
.inputfile span {
	position: relative;
	display: inline-block;
	cursor: pointer;
	outline: none;
	text-decoration: none;
	font-size: 14px;
	vertical-align: middle;
	color: black;
	text-align: center;
	
	background-color: white;
	line-height: 22px;
	height: 30px;
	padding:5px 5px;
	box-sizing: border-box;
	border: none;
	margin: 0;
	transition: background-color 0.2s;
}
.inputfile input[type=file] {
	position: absolute;
	z-index: -1;
	opacity: 0;
	display: block;
	width: 0;
	height: 0;
}
 
/* Focus */

 
/* Hover/active */
.inputfile:hover span {
	background-color: rgb(200,200,200);
}
.inputfile:active span {
	background-color: white
}
 
/* Disabled */
.inputfile input[type=file]:disabled + span {
	background-color: #eee;
}